body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    min-height: 100vh;
}

#root {
    min-height: 100vh;
}


a.hide-link:link {
    text-decoration: none;
}
a.hide-link:visited {
    text-decoration: none;
}
a.hide-link:hover {
    text-decoration: none;
}
a.hide-link:hover .title {
    text-decoration: underline;
}
a.hide-link:active .title{
    text-decoration: underline;
}
a.hide-link:active{
    text-decoration: none;
}
a:hover h2.link-with-underline {
    text-decoration: underline;
}

.App {
  text-align: center;
  background-color: #eeeeee;
  min-height: 100vh;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

p {
  text-align: justify;
  text-justify: inter-word;
}
.app-content {
  min-height: 100vh;
}
.rotate-180 {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.header-logo {
  max-height: 50px;
  width: 100%;
}

.app-parent {
  min-height: 100vh;
  padding-bottom: 5rem;
}

.gdprBanner {
    position: fixed;
    bottom: 0;
    background-color: #cccccc;
    opacity: 0.9;
    min-height: 80px;
    width: 80%;
    padding: 1rem 1rem 0.5rem 1rem;
    z-index: 1000;
    margin-left: 10%;
    border-radius: 10px;
    margin-bottom: 0.5rem;
}

.gdprBanner > p {
  text-align: center;
}

.gdprBanner > button {
    margin-top: 0.5rem;
}

@media (max-width: 1330px) {
    .gdprBanner {
        width: 98%;
        margin-left: 1%;
    }
}


.progress-bar-custom {
  width:0%;
  height:2px;
  background-color: var(--toc-color);
  background-size:350px 5px;
  border-radius:1px;
  margin: 0 5px;
  margin-top:10px;
  -webkit-animation:loading 7s linear forwards;
          animation:loading 7s linear forwards;
}
@-webkit-keyframes loading {
  to {
    width: calc(100% - 10px);
  }
}
@keyframes loading {
  to {
    width: calc(100% - 10px);
  }
}
.filling-empty-space {
    width:412px;
    height:0;
}

.search-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 3rem;
    padding-left: 12vw;
    padding-right: 12vw;
    margin-bottom: 2rem;
}
@media screen and (max-width: 1400px) {
    .search-bar {
        padding-left: 8vw;
        padding-right: 8vw;
    }
}
@media screen and (max-width: 1015px) {
    .search-bar {
        padding-left: 5vw;
        padding-right: 5vw;
    }
}

@media screen and (max-width: 630px) {
    .search-bar {
        padding-left: 4vw;
        padding-right: 4vw;
    }
}

.search-bar > button, .search-bar > label {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
}

.search-bar-icon {
    display: flex;
    align-items: center;
}

.search-bar-icon > .material-icons {
    margin-top: -6px;
}

.search-tags {
    min-width: 300px;
}

.article-wrapper {
    max-width: 1000px;
    margin-left: 0;
    min-width: 100%;
    padding: 1rem 1rem 3rem;
}

.similars-list-wrapper {
    padding-top: 2rem;
    margin-bottom: 3.5rem;
    width: 100%;
    border-top: 1px solid #ccc;
}

.similars-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    scroll-behavior: smooth;
}

/* Ecrans > 960px */
@media screen and (min-width: 960px) {
    .article-page-wrapper {
        justify-content: space-between !important;
    }
    .article-wrapper {
        min-width: 53%;
        padding: 1rem 1rem 3rem 1rem;
        flex-grow: 2;
        flex-basis: 20%;
    }
    .table-of-content-wrapper {
        width: calc(20% + 20px);
    }
    .table-of-content-container {
        top: 0;
        position: fixed;
        -webkit-transition: left ease-out 0.5s;
        transition: left ease-out 0.5s;
        overflow-x: hidden;
    }
    .table-of-content {
        border-right: #ccc 1px solid;
        padding-left:10px;
        padding-right:15px;
        padding-top: 0.6rem;
    }
}
/* Ecrans > 1400 */
@media screen and (min-width: 1400px) {    
    .similars-list-container {
        width: calc(20% - 1rem);
        border-left: 1px solid #ccc;
        padding-left: 15px;
    }
    .similars-list-wrapper {
        width: 100%;
        overflow: hidden;
        border-top: none;
    }
    .similars-list {
        width: calc(100% + 20px);
        overflow-y: auto;
        max-height: calc(100vh - 64px - 52px - 2rem);
        padding-right: 20px;
        border-bottom: 1px dashed #ccc;
    }
    .table-of-content-wrapper {
        width: calc(15% + 20px);
    }
}
/* Ecrans > 1600 */
@media screen and (min-width: 1600px) {
    .similars-list-wrapper {
        margin-left: 0;
    }
}

.article-page-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.article-content {
    min-width: 300px;
    font-size: 1.25rem;
}

.article-content p {
    line-height: 2rem;
    margin-top: 2rem;
    text-indent: 5em;
}

.article-content h2, .article-content h3, .article-content h4 {
    margin-top: 3.5rem;
}

.article-wrapper img {
    width: auto;
    max-width: 100%;
    max-height: 80vh;
    height: auto;
}

.article{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: flex-start;
}

.article-footer {
    width: 100%;
    text-align: left;
    margin-top: 3rem;
    position: relative
}
.footer-separator-top::before {
    background: -webkit-gradient(linear, left top, right top, from(var(--main-color)), to(var(--background-color)));
    background: linear-gradient(to right, var(--main-color), var(--background-color) 100%);
    content: '';
    position: absolute;
    border-radius: 3px;
    min-width: 310px;
    width: 80%;
    height: 5px;
    top: -1.4rem;
    left: -5px;
}

.article-description {
    color: black;
}


.article-infos {
    text-align: left;
    width: 100%;
    font-size: 12px;
    margin-bottom: 2rem;
    margin-top: 1rem;
}

.folder-children-list {
    margin-top: 3rem;
    position: relative;
}
.folder-children-list::before {
    background: -webkit-gradient(linear, left top, right top, from(var(--main-color)), to(var(--background-color)));
    background: linear-gradient(to right, var(--main-color), var(--background-color) 100%);
    content: '';
    position: absolute;
    border-radius: 3px;
    min-width: 310px;
    width: 80%;
    height: 5px;
    top: -1.4rem;
    left: -5px;
}
.folder-children-list::after {
    background: -webkit-gradient(linear, right top, left top, from(var(--main-color)), color-stop(80%, var(--background-color)));
    background: linear-gradient(to left, var(--main-color), var(--background-color) 80%);
    content: '';
    position: absolute;
    border-radius: 3px;
    min-width: 310px;
    width: 100%;
    height: 5px;
    bottom: -1.7rem;
    left: -5px;
}

.folder-brother-list {
    margin-top: 3rem;
    position: relative;
}
.folder-brother-list::before {
    background: -webkit-gradient(linear, left top, right top, from(var(--main-color)), to(var(--background-color)));
    background: linear-gradient(to right, var(--main-color), var(--background-color) 100%);
    content: '';
    position: absolute;
    border-radius: 3px;
    min-width: 310px;
    width: 80%;
    height: 5px;
    top: -1.4rem;
    left: -5px;
}
.folder-brother-list::after {
    background: -webkit-gradient(linear, right top, left top, from(var(--main-color)), color-stop(80%, var(--background-color)));
    background: linear-gradient(to left, var(--main-color), var(--background-color) 80%);
    content: '';
    position: absolute;
    border-radius: 3px;
    min-width: 310px;
    width: 100%;
    height: 5px;
    bottom: -2rem;
    left: -5px;
}


.brother-list {
    margin-top: 1.5rem;
    margin-bottom: .5rem;
}
.brother-list > div > div, .brother-list > div > a{
    max-width: 50%;
}
.brother-list > div span.material-icons{
    margin-right: 1rem;
    margin-left: 1rem;
}
.brother-list > div a span:not(.material-icons){
    text-decoration: underline !important;
}

.folder-name {
    margin-top: 1rem;
}

.donation-button {
    width: 100%;
    padding: 2rem 1rem 2rem 1rem;
}
.donation-button form {
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.image-legend {
    margin-bottom: 3.5rem;
}

p {
    font-size: 1rem !important;
}

h2 {
    font-size: 1.5rem !important;
    font-weight: 400 !important;
}

h3 {
    font-size: 1.25rem !important;
    font-weight: 400 !important;
}

h4 {
    font-size: 1.1rem !important;
    font-weight: 400 !important;
}

.mt-2 {
    margin-top: 2rem;
}
.text-align-left {
    text-align: left;
}
.toc-list, .toc-list > ol{
    list-style-position: inside;
    text-align: left;
    counter-reset: ol-number;
    list-style:none;
}
.toc-list-item {
    margin-top: 0.4rem;
}
.toc-list-item::before {
    content: counters(ol-number, ".") " ";
    counter-increment: ol-number
}
.toc-list{
    margin:0;
    padding-left:10px
}
a.toc-link{
    color:currentColor;
    height: 100%;
    max-height:60px;
    text-decoration: none;
    line-height: 1.4;
}
.is-collapsible{
    max-height: 600px;
    overflow: hidden;
    -webkit-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out
}
.is-collapsed{
    max-height:0;
}
.is-active-link{
    font-weight:700
}
.toc-link::before{
    background-color: #ccc;
    content: ' ';
    display:inline-block;
    height:inherit;
    left:0;
    margin-top:-1px;
    position:absolute;
    width:2px;
}
.is-active-link::before{
    background-color: var(--toc-color);
}
.toc-here {
    position: relative;
    overflow: hidden;
}
.toc-wrapper {
    overflow-y: auto;
    max-height: calc(100vh - 70px - 75px);
}

h2::before, h3::before, h4::before, h5::before, h6::before {
    display: block;
    content: " ";
    height: 1.5rem;
    margin-top: -1.5rem;
    visibility: hidden;
}

h2:focus, h3:focus, h4:focus, h5:focus, h6:focus {
    outline: none
}
.medium-zoom--opened .medium-zoom-overlay {
    opacity: 0.9 !important;
}
