body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    min-height: 100vh;
}

#root {
    min-height: 100vh;
}


a.hide-link:link {
    text-decoration: none;
}
a.hide-link:visited {
    text-decoration: none;
}
a.hide-link:hover {
    text-decoration: none;
}
a.hide-link:hover .title {
    text-decoration: underline;
}
a.hide-link:active .title{
    text-decoration: underline;
}
a.hide-link:active{
    text-decoration: none;
}
a:hover h2.link-with-underline {
    text-decoration: underline;
}
