.filling-empty-space {
    width:412px;
    height:0;
}

.search-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 3rem;
    padding-left: 12vw;
    padding-right: 12vw;
    margin-bottom: 2rem;
}
@media screen and (max-width: 1400px) {
    .search-bar {
        padding-left: 8vw;
        padding-right: 8vw;
    }
}
@media screen and (max-width: 1015px) {
    .search-bar {
        padding-left: 5vw;
        padding-right: 5vw;
    }
}

@media screen and (max-width: 630px) {
    .search-bar {
        padding-left: 4vw;
        padding-right: 4vw;
    }
}

.search-bar > button, .search-bar > label {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
}

.search-bar-icon {
    display: flex;
    align-items: center;
}

.search-bar-icon > .material-icons {
    margin-top: -6px;
}

.search-tags {
    min-width: 300px;
}
