.App {
  text-align: center;
  background-color: #eeeeee;
  min-height: 100vh;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

p {
  text-align: justify;
  text-justify: inter-word;
}
.app-content {
  min-height: 100vh;
}
.rotate-180 {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

.header-logo {
  max-height: 50px;
  width: 100%;
}

.app-parent {
  min-height: 100vh;
  padding-bottom: 5rem;
}

.gdprBanner {
    position: fixed;
    bottom: 0;
    background-color: #cccccc;
    opacity: 0.9;
    min-height: 80px;
    width: 80%;
    padding: 1rem 1rem 0.5rem 1rem;
    z-index: 1000;
    margin-left: 10%;
    border-radius: 10px;
    margin-bottom: 0.5rem;
}

.gdprBanner > p {
  text-align: center;
}

.gdprBanner > button {
    margin-top: 0.5rem;
}

@media (max-width: 1330px) {
    .gdprBanner {
        width: 98%;
        margin-left: 1%;
    }
}


.progress-bar-custom {
  width:0%;
  height:2px;
  background-color: var(--toc-color);
  background-size:350px 5px;
  border-radius:1px;
  margin: 0 5px;
  margin-top:10px;
  animation:loading 7s linear forwards;
}
@keyframes loading {
  to {
    width: calc(100% - 10px);
  }
}